// Blog.js
import axios from "axios";
import Footer from "components/footer/Footer";
import Navbar from "components/navbar/Navbar";
import WithPadding from "components/shared/WithPadding";
import moment from "moment";
import { useEffect, useState } from "react";
import { BsLinkedin } from "react-icons/bs";
import { FaFacebook, FaInstagramSquare, FaYoutube } from "react-icons/fa";
import { useNavigate, useParams } from "react-router-dom";
import "swiper/components/navigation/navigation.min.css";
import SwiperCore, { Autoplay, Navigation } from "swiper/core";
import "swiper/swiper.min.css";
import { postRequest } from "utils/api";
import verified from "../../assets/images/main/Icon.svg";
import style from "./Blog.module.scss";
import BlogPreview from "./BlogPreview";

SwiperCore.use([Navigation, Autoplay]);
const images = [
  {
    img: <FaFacebook size={31} style={{ color: "#4B9BEE" }} />,
    link: "https://www.facebook.com/",
  },
  {
    img: <FaInstagramSquare size={31} style={{ color: "#4B9BEE" }} />,
    link: "https://www.instagram.com/careertest_official/",
  },
  {
    img: <FaYoutube size={31} style={{ color: "#4B9BEE" }} />,
    link: "https://in.linkedin.com/company/vlead-eduventures",
  },
  {
    img: <BsLinkedin size={31} style={{ color: "#4B9BEE" }} />,
    link: "https://in.linkedin.com/company/vlead-eduventures",
  },
];
const RichTextComponent = ({ content }) => {
  return (
    <div>
      {/* Use dangerouslySetInnerHTML to parse and render the HTML content */}
      <div dangerouslySetInnerHTML={{ __html: content }} />
    </div>
  );
};

export default function Blog() {
  const navigateTo = useNavigate();
  const { slug } = useParams();
  const [blog, setBlog] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [formData, setFormData] = useState({});

  async function handleGetStarted() {
    const { email } = formData;

    postRequest(
      "/web/subscriptions",
      {
        email,
      },
      { noAuth: true },
    )
      .then(() => {
        navigateTo(`/auth/register?email=${email}`);
      })
      .catch((e) => {
        console.error(e);
      });
  }

  function handleChangeForm(e) {
    const { value } = e.target;
    setFormData({ email: value });
  }

  useEffect(() => {
    setIsLoading(true);
    async function axio() {
      const resp = await axios.get(
        "https://console.careertest.ai/api/web/blogs/",
      );
      const matchingBlog = resp.data.find((b) => b.slug === slug);
      console.log(matchingBlog);
      if (matchingBlog) {
        setBlog(matchingBlog);
      } else {
        // Handle case when no matching blog is found
        console.log(`No blog found for slug: ${slug}`);
      }
      setIsLoading(false);
    }
    axio();
  }, [slug]);

  return (
    <>
      <Navbar background={"white"} />

      <div className={style.wrapper}>
        <div className={style.coverImageContainer}>
          <img src={blog.background} alt="Cover" />
          <div className={style.titleOverlay}>
            <h1>{blog.title}</h1>
          </div>
        </div>{" "}

        <WithPadding className={style.blog_container}>
          {isLoading ? (
            <p>Loading...</p>
          ) : (
            <div className={style.blog_content}>
              <div className={style.span}>
                <h4>Share</h4>
                {images.map((image) => (
                  <span
                    onClick={() => {
                      window.open(image.link);
                    }}
                  >
                    {image.img}
                  </span>
                ))}
              </div>
              <div className={style.subcontainer}>
                <div className={style.summary}>{blog.summary}</div>
                <div className={style.authorSection}>
                  <img
                    src={blog.image}
                    alt={`Author:"`}
                    className={style.authorPhoto}
                  />
                  <div className={style.groupAuthorNameSection}>
                    <div className={style.name}>
                      <p className={style.authorName}>{blog?.author?.name}</p>
                      <span className={style.verified}>
                        {" "}
                        <img src={verified} className={style.icons} /> Verified
                        Writer
                      </span>
                    </div>
                    <div className={style.date}>
                      <p style={{ color: "#5A7184", fontSize: "14px" }}>
                        Published On:  {moment(blog.date).format("MMM,D YYYY")}
                      </p>
                    </div>
                  </div>
                </div>
                <img
                  src={blog.image}
                  alt={blog.title}
                  className={style.imageonsummary}
                />

                <div>
                  <div className={style.mainheading}>{blog.title}</div>
                  <div className={style.subheadingdata}>
                    <RichTextComponent content={blog.content} />
                  </div>
                </div>
              </div>
            </div>
          )}
        </WithPadding>

        <BlogPreview />

        <Footer />
      </div>
    </>
  );
}
